<template>
    <div>
        <el-main>
            <div class="select">
                <el-select v-model="department" clearable filterable placeholder="">
                    <el-option
                        v-for="item in departmentList"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                    ></el-option>
                </el-select>
                <el-select v-model="degree" clearable filterable placeholder="">
                    <el-option
                        v-for="item in degreeList"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                    ></el-option>
                </el-select>
                <el-date-picker
                    v-model="stime"
                    type="daterange"
                    range-separator="-"
                    start-placeholder=""
                    end-placeholder
                ></el-date-picker>
                <el-date-picker
                    v-model="etime"
                    type="daterange"
                    range-separator="-"
                    start-placeholder=""
                    end-placeholder
                ></el-date-picker>
                <el-input v-model="search" placeholder="" clearable></el-input>
            </div>
            <div class="btn">
                <el-button type="primary" icon="el-icon-plus" @click="add()"></el-button>
                <el-button type="primary" icon="el-icon-document"></el-button>
                <el-button type="primary" icon="el-icon-document" @click="outlist()"></el-button>
                <el-link type="primary" style="float:right" @click="download()"></el-link>
            </div>
            <el-table
                border
                ref="multipleTable"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange"
                :header-cell-style="{background:'#f5f7fa'}"
                :default-sort="{prop: 'wid', order: 'descending'}"
                :data="tableData"
            >
                <el-table-column type="selection" width="55"></el-table-column>
                <el-table-column prop="wid" label="" min-width="100" sortable></el-table-column>
                <el-table-column prop="name" label="" min-width="100"></el-table-column>
                <el-table-column
                    prop="sex"
                    label=""
                    min-width="100"
                    :filters="[{ text: '', value: '0' }, { text: '', value: '1' }]"
                    :filter-method="filterSex"
                    filter-placement="bottom-end"
                >
                    <template slot-scope="scope">
                        <el-tag
                            :type="scope.row.sex === '0' ? 'primary' : 'warning'"
                            disable-transitions
                        >{{scope.row.sex=='1'?"":""}}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="degree" label="" min-width="100"></el-table-column>
                <el-table-column prop="department" label="" min-width="100"></el-table-column>
                <el-table-column prop="phone" label="" min-width="100"></el-table-column>
                <el-table-column
                    prop="state"
                    label=""
                    min-width="100"
                    :filters="[{ text: '', value: '' }, { text: '', value: '' }]"
                    :filter-method="filterState"
                    filter-placement="bottom-end"
                >
                    <template slot-scope="scope">
                        <el-popover
                            placement="top-start"
                            width="150"
                            trigger="hover"
                            :content="'' + scope.row.signingTime + '' + scope.row.departureTime"
                        >
                            <el-button slot="reference" class="bbb">
                                <el-tag
                                    :type="scope.row.state === '' ? 'success' : 'danger'"
                                    disable-transitions
                                >{{scope.row.state}}</el-tag>
                            </el-button>
                        </el-popover>
                    </template>
                </el-table-column>
                <el-table-column min-width="130" label="">
                    <template slot-scope="scope">
                        <el-button
                            size="mini"
                            type="primary"
                            @click="handleEdit(scope.$index, scope.row)"
                        ></el-button>
                        <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.$index, scope.row)"
                        ></el-button>
                    </template>
                </el-table-column>
            </el-table>

            <el-pagination
                style="float: right;margin-top:8px;"
                background
                :current-page.sync="currentPage"
                @current-change="handleCurrentChange"
                layout="prev, pager, next"
                :page-size="7"
                :total="totalPage"
            ></el-pagination>
        </el-main>
        <edit :edititem="edititem" :editVisible="editVisible" @close-dialogStatus="Close_dialog"></edit>
        <file :fileitem="fileitem" :fileVisible="fileVisible" @close-dialogStatus="Close_dialog"></file>

        <!--  -->
        <el-table id="out-table" :data="outtableData" height="0">
            <el-table-column prop="wid" label=""></el-table-column>
            <el-table-column prop="name" label=""></el-table-column>
            <el-table-column prop="sex" label=""></el-table-column>
            <el-table-column prop="degree" label=""></el-table-column>
            <el-table-column prop="department" label=""></el-table-column>
            <el-table-column prop="phone" label=""></el-table-column>
        </el-table>
    </div>
</template>
<script>
import edit from "@/components/dialog/edit";
import file from "@/components/dialog/file";
import router from "../router";
import moment from "moment";
import FileSaver from "file-saver";
import XLSX from "xlsx";
import axios from "axios";
export default {
    data() {
        return {
            search: "",
            edititem: {},
            editVisible: false,
            fileitem: {},
            fileVisible: false,
            currentPage: 1,
            totalPage: 1,

            departmentList: [
                {
                    label: "1",
                    value: "0"
                },
                {
                    label: "2",
                    value: "1"
                },
                {
                    label: "3",
                    value: "2"
                }
            ],
            department: "",

            degreeList: [
                { label: "", value: "1" },
                { label: "", value: "3" },
                { label: "", value: "2" },
                { label: "", value: "4" },
                { label: "", value: "5" }
            ],
            degree: "",

            stime: [],
            etime: [],

            tableData: [],
            outtableData: [],
            multipleSelection: []
        };
    },
    created: function() {
        this.getList();
    },
    mounted: function() {},
    watch: {
        department(newValue, oldValue) {
            this.getList();
        },
        stime(newValue, oldValue) {
            this.getList();
        },
        etime(newValue, oldValue) {
            this.getList();
        },
        degree(newValue, oldValue) {
            this.getList();
        },
        search(newValue, oldValue) {
            this.getList();
        }
    },
    components: {
        edit,
        file
    },
    methods: {
        getList() {
            let here = this;
            axios({
                url: "/list",
                params: {
                    department: here.department,
                    degree: here.degree,
                    search: here.search,
                    stime: here.stime==''?'':moment(here.stime[0]).format("YYYY-MM-DD") + ',' + moment(here.stime[1]).format("YYYY-MM-DD"),
                    etime: here.etime==''?'':moment(here.etime[0]).format("YYYY-MM-DD") + ',' + moment(here.etime[1]).format("YYYY-MM-DD"),
                    currentPage: here.currentPage
                }
            })
                .then(function(response) {
                    let data = response.data;
                    here.tableData = data.data;
                    here.totalPage = data.totalPage;
                })
                .catch(function(error) {
                    console.log(error);
                });
        },
        Close_dialog(val) {
            this.editVisible = false;
            this.fileVisible = false;
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        filterSex(value, row) {
            return row.sex === value;
        },
        filterState(value, row) {
            return row.state === value;
        },
        add() {
            router.push({
                name: "tabs",
                params: {
                    data: "0",
                    index: 1
                }
            });
        },
        handleEdit(index, row) {
            router.push({
                name: "tabs",
                params: {
                    data: row,
                    index: 1
                }
            });
        },
        handleDelete(index, row) {
            let here = this;
            if (row.state === "") {
                this.$message.error("!");
                return;
            }
            this.$confirm("?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning"
            }).then(() => {
                axios({
                    url: "/delete",
                    params: {
                        wid: row.wid
                    }
                })
                    .then(function(response) {
                        if(response.data.success===1){
                            this.$message({
                            type: "success",
                            message: "!"
                        });
                        this.getList();
                        } else {
                            this.$message.error("!");
                        }
                    })
                    .catch(function(error) {
                        console.log(error);
                    });
            });
        },
        handleCurrentChange() {
            this.getList();
        },
        outlist() {
            let here = this;
            axios({
                url: "/outlist",
                params: {
                    department: here.department,
                    degree: here.degree,
                    search: here.search,
                    stime: here.stime==''?'':moment(here.stime[0]).format("YYYY-MM-DD") + ',' + moment(here.stime[1]).format("YYYY-MM-DD"),
                    etime: here.etime==''?'':moment(here.etime[0]).format("YYYY-MM-DD") + ',' + moment(here.etime[1]).format("YYYY-MM-DD")
                }
            })
                .then(function(response) {
                    let data = response.data;
                    here.outtableData = data;
                })
                .catch(function(error) {
                    console.log(error);
                });
            exportExcel();
        },
        exportExcel() {
            /*  */
            var wb = XLSX.utils.table_to_book(
                document.querySelector("#out-table")
            );
            /*  */
            var wbout = XLSX.write(wb, {
                bookType: "xlsx",
                bookSST: true,
                type: "array"
            });
            try {
                FileSaver.saveAs(
                    //Blob 
                    //Blob JavaScript
                    //File Blob blob 
                    // Blob 
                    new Blob([wbout], { type: "application/octet-stream" }),
                    //
                    "user.xlsx"
                );
            } catch (e) {
                if (typeof console !== "undefined") console.log(e, wbout);
            }
            return wbout;
        },
        download() {
            window.open("http://localhost:8080/download");
        }
    }
};
</script>

<style lang="stylus" scoped>
body {
    margin: 0;
}

.bottom {
    margin-top: 13px;
    line-height: 12px;
}

.button {
    padding: 0;
    float: right;
}

.image {
    width: 100%;
    height: 100%;
    display: block;
}

.clearfix:before, .clearfix:after {
    display: table;
    content: '';
}

.clearfix:after {
    clear: both;
}

.btn {
    margin-bottom: 10px;
}

.select {
    display: inline;
    margin-bottom: 10px;
}

.select, .el-input {
    width: 156px;
    margin-bottom: 10px;
}

.el-date-editor--daterange.el-input, .el-date-editor--daterange.el-input__inner, .el-date-editor--timerange.el-input, .el-date-editor--timerange.el-input__inner {
    width: 240px;
}

.bbb {
    cursor: text;
    width: 41px;
    height: 23px;
    padding: 0;
    border: 0px;
}
</style>



