<template>
    <div>
        <el-dialog
            :title="info.name + ''"
            :visible.sync="dialogfileVisible"
            @close="closeDialog"
        >
            <div class="credentials">
                <el-upload
                    action="https://jsonplaceholder.typicode.com/posts/"
                    :on-success="handleAvatarSuccess"
                    :show-file-list="false"
                >
                    <el-select v-model="value" placeholder="">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :value="item.value"
                        ></el-option>
                    </el-select>
                    <el-button size="small" type="primary"></el-button>
                </el-upload>
                <el-table :data="tableData">
                    <el-table-column property="filename" label="" width="150"></el-table-column>
                    <el-table-column property="filetype" label="" width="200"></el-table-column>
                    <el-table-column property="filesize" label=""></el-table-column>
                    <el-table-column label="">
                        <template slot-scope="scope">
                            <el-button
                                size="mini"
                                @click="handleDownload(scope.$index, scope.row)"
                            ></el-button>
                            <el-button
                                size="mini"
                                type="danger"
                                @click="handleDelete(scope.$index, scope.row)"
                            ></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            info: {},
            dialogfileVisible: false,
            options: [
                {value:""},
                {value:""},
                {value:""},
            ],
            value:'',
            credentialslist: [
                {
                    name: "test",
                    url: "https://i.loli.net/2019/07/12/5d285aa60cf6474023.gif"
                }
            ],
            tableData: [
                {
                    filename: "20190909",
                    filetype: "",
                    filesize: "200kb"
                },
                {
                    filename: "20180806",
                    filetype: "",
                    filesize: "400kb"
                },
                {
                    filename: "20175050",
                    filetype: "",
                    filesize: "50kb"
                }
            ]
        };
    },
    props: {
        fileVisible: Boolean,
        fileitem: Object
    },
    watch: {
        fileVisible(newValue, oldValue) {
            this.dialogfileVisible = newValue;
        },
        fileitem(newValue, oldValue) {
            this.info = newValue;
        }
    },
    methods: {
        closeDialog() {
            this.$emit("close-dialogStatus", true);
        },
        handleAvatarSuccess(res, file) {
            //this.imageUrl = URL.createObjectURL(file.raw);
        },
        handleDownload(index, row) {
            console.log(index, row);
        },
        handleDelete(index, row) {
            console.log(index, row);
        }
    }
};
</script>

<style>
</style>



