<template>
    <div>
        <el-dialog :title="ruleForm.wid=='-1'?'':''" :visible.sync="dialogeditVisible" @close="closeDialog">
            <el-form
                :model="ruleForm"
                status-icon
                :rules="rules"
                ref="ruleForm"
                label-width="100px"
            >
                <el-row>
                    <el-col :span="4">
                        <el-tooltip class="item" effect="dark" content="" placement="top-start">
                        <el-upload
                            class="avatar-uploader"
                            action="https://jsonplaceholder.typicode.com/posts/"
                            :show-file-list="false"
                            :on-success="handleAvatarSuccess"
                            :before-upload="beforeAvatarUpload"
                        >
                            <img v-if="ruleForm.img" :src="ruleForm.img" class="avatar" />
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                        </el-tooltip>
                    </el-col>
                    <el-col :span="20">
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="" prop="name">
                                    <el-input v-model="ruleForm.name" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="" prop="sex">
                                    <el-input v-model="ruleForm.sex" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="" prop="degree">
                                    <el-input v-model="ruleForm.degree" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="" prop="title">
                                    <el-input v-model="ruleForm.title" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="" prop="nation">
                                    <el-input v-model="ruleForm.nation" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="" prop="origin">
                                    <el-input v-model="ruleForm.origin" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="" prop="identity">
                                    <el-input v-model="ruleForm.identity" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="" prop="department">
                                    <el-select v-model="ruleForm.department">
                                        <el-option
                                            v-for="item in departmentList"
                                            :key="item.value"
                                            :label="item.label"
                                            :value="item.value"
                                        ></el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="" prop="card">
                                    <el-input v-model="ruleForm.card" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="" prop="wages">
                                    <el-input v-model="ruleForm.wages" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="" prop="phone">
                                    <el-input v-model="ruleForm.phone" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="" prop="email">
                                    <el-input v-model="ruleForm.email" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="" prop="signingTime">
                                    <el-input v-model="ruleForm.signingTime" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="" prop="departureTime">
                                    <el-input v-model="ruleForm.departureTime" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-col>
                </el-row>
                <el-row>
                    <el-form-item style="text-align: right;">
                        <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                        <el-button @click="dialogeditVisible=false;"></el-button>
                    </el-form-item>
                </el-row>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
import axios from "axios";
export default {
    data() {
        return {
            dialogeditVisible: false,
            departmentList: [
                {
                    value: "1",
                    label: ""
                },
                {
                    value: "2",
                    label: ""
                },
                {
                    value: "3",
                    label: ""
                }
            ],
            ruleForm: {
                wid: "",
                name: "",
                sex: "",
                img: "",
                identity: "",
                department: "",
                phone: "",
                state: "",
                signingTime: "",
                departureTime: "",
                degree: "",
                title:"",
                nation: "",
                Origin: "",
                email: "",
                wages: "",
                card: ""
            },
            rules: {
                name: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                sex: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                identity: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                department: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                phone: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                signingTime: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                departureTime: [
                    {
                        message: "",
                        trigger: "blur"
                    }
                ],
                degree: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                card: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                wages: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ]
            }
        };
    },
    props: {
        editVisible: Boolean,
        edititem: Object
    },
    watch: {
        editVisible(newValue, oldValue) {
            this.dialogeditVisible = newValue;
        },
        edititem(newValue, oldValue) {
            this.ruleForm = newValue;
        }
    },
    methods: {
        closeDialog() {
            this.$emit("close-dialogStatus", true);
        },
        submitForm(ruleForm) {
            this.dialogeditVisible = false;
            this.$message({
                type:"success",
                message:""
            });
        },
        handleAvatarSuccess(res, file) {
            //this.imageUrl = URL.createObjectURL(file.raw);
        },
        beforeAvatarUpload(file) {
            this.ruleForm.img = URL.createObjectURL(file);
            const isLt2M = file.size / 1024 / 1024 < 2;
            if (!isLt2M) {
                this.$message.error(" 2MB!");
            }
        }
    }
};
</script>

<style lang="stylus" scoped>
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}

.avatar-uploader .el-upload:hover {
    border-color: #409eff;
}

.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
}

.avatar {
    width: 137px;
    height: 169px;
    display: block;
}
</style>


